import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import ComplexHeader from './components/TimeSheet.vue'
import Login from './components/LoginBox.vue'
import Employees from './components/Employees.vue'
import JobView from './views/JobView.vue'
import Register from './components/Register.vue'
import HourList from './views/HourList.vue'
import Dashboard from './views/Dashboard.vue'
import store from './store'

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login,
    },
    {
      path: '/Metrics',
      name: 'Metrics',
      component: Dashboard,
	  beforeEnter(to, from, next) {
        if (localStorage.getItem('role')=== '1') {
     		next()
  		} 
		else {
    		next({
      			name: "login" // back to safety route //
    		});
      	}
	  }
    },
    {
      path:'/Jobs',
      name: 'Jobs',
      component: JobView,
	  beforeEnter(to, from, next) {
        if (localStorage.getItem('role')=== '1') {
     		next()
  		} 
		else {
    		next({
      			name: "login" // back to safety route //
    		});
      	}
	  }
    },
    {
      path: '/Employees',
      name: 'Employees',
      component: Employees,
	  beforeEnter(to, from, next) {
        if (localStorage.getItem('role')=== '1') {
     		next()
  		} 
		else {
    		next({
      			name: "login" // back to safety route //
    		});
      	}
	  }
    },
    {
      path: '/TimeSheet',
      name: 'Time Sheet',
      component: ComplexHeader,
    },
    { 
      path: '/Login',
      name: 'Login',
      component: Login,
    },
    { 
      path: '/Register',
      name: 'Register',
      component: Register,
	  beforeEnter(to, from, next) {
        if (localStorage.getItem('role')=== '1') {
     		next()
  		} 
		else {
    		next({
      			name: "login" // back to safety route //
    		});
      	}
	  }
    },
    { 
      path: '/HourList',
      name: 'Hour List',
      component: HourList,
	  beforeEnter(to, from, next) {
        if (localStorage.getItem('role')=== '1') {
     		next()
  		} 
		else {
    		next({
      			name: "login" // back to safety route //
    		});
      	}
	  }
    },
  ],
});
