<template>
  <div>
    <div>
      <div class='dashboard parent'>
	      <div class='col'>
          <h2>Hours Worked By Job</h2>
          <apexcharts width="1000" class="charts" type="bar" :options="JobHoursOptions" :series="JobHoursSeries"></apexcharts>
          <h2>Cost By Job</h2>
          <apexcharts width="1000" class="charts" type="bar" :options="JobCostOptions" :series="JobCostSeries"></apexcharts>
          <h2>Hours Worked this {{search.Unit}}</h2>
          <apexcharts width="1000" class="charts" type="bar" :options="options2" :series="series2"></apexcharts>
          <h2>Invoices Received this </h2>
          <apexcharts width="1000" class="charts" type="bar" :options="options3" :series="series3"></apexcharts>
           <h2>Total Expenses on Active Projects</h2>
          <apexcharts width="1000" class="charts" type="bar" :options="options" :series="series"></apexcharts>
  	    </div>
	      <div class='list col'>
	        <div class="filter-list parent">
            <div class="col">
              <v-combobox v-model="search.People" multiple label="Employee" :items="employees" />
            </div>
            <div class="col">
              <v-combobox v-model="search.Client" multiple label="Client" :items="clients" />
            </div>
            <div class="col">
              <v-combobox v-model="search.Job" multiple label="Job" :items="jobs" />
            </div>
            <div class="col">
              <v-combobox v-model="search.Unit" label="Unit" :items="['Week','Month']" />
            </div>
            <div class="col">
              <v-text-field v-model="search.Amount" name="amount" label="Amount" type="number"></v-text-field>
            </div>
            <v-btn @click="importData2(); importData(); importData3(); JobHOursMetric(); JobCostMetric();">Apply</v-btn>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
import VueApexCharts from "vue-apexcharts";
export default {
  components: {
    apexcharts: VueApexCharts
  },
  data() {
    return {
      options: {},
      series: [],
      options2: {},
      series2: [],
      options3: {},
      series3: [],
      JobHoursOptions : {},
      JobHoursSeries: [],
      JobCostOptions : {},
      JobCostSeries: [],
      search: {
        Client: [],
        Job: [],
        People: [],
        Unit: "Month",
        Amount: 12
      }
    };
  },
  computed: {
    employees() {
      return this.$store.getters.getEmployeeList;
    },
    clients() {
      let jobs = this.$store.getters.getJobs;
      var clients = [];
      for (var i = 0; i < jobs.length; i++) {
        clients.push(jobs[i].Job.Client);
      }
      return clients;
    },
    jobs() {
      let jobsList = this.$store.getters.getJobs;
      var jobs = [];
      for (var i = 0; i < jobsList.length; i++) {
        jobs.push(jobsList[i].Job.Name);
      }
      return jobs;
    }
  },
  methods: {
    importData() {
      const path = "http://13.55.48.180:5000/metrics/expenses";

      Axios.post(path, this.search).then(res => {
        this.options = res.data.options;
        this.series = res.data.series;
      });
    },
    importData2() {
      const path = "http://13.55.48.180:5000/metrics/hours";
      Axios.post(path, this.search).then(res => {
        this.options2 = res.data.options;
        this.series2 = res.data.series;
      });
    },
    importData3() {
      const path = "http://13.55.48.180:5000/metrics/invoice";
      Axios.post(path, this.search).then(res => {
        this.options3 = res.data.options;
        this.series3 = res.data.series;
      });
    },
    JobHOursMetric() {
      const path = "http://13.55.48.180:5000/metrics/hours/job";
      Axios.post(path, this.search).then(res => {
        this.JobHoursOptions = res.data.options;
        this.JobHoursSeries = res.data.series;
      });
    },
    JobCostMetric() {
      const path = "http://13.55.48.180:5000/metrics/cost/job";
      Axios.post(path, this.search).then(res => {
        this.JobCostOptions = res.data.options;
        this.JobCostSeries = res.data.series;
      });
    }
  },
  mounted() {
    Axios.defaults.headers.common.Authorization = `Bearer ${localStorage.getItem(
      "token"
    )}`;
    this.Id = localStorage.getItem("Id");
    this.importData();
    this.importData2();
    this.importData3();
    this.JobHOursMetric();
    this.JobCostMetric();
  }
};
</script>

<style scoped>
  .filter-list {
    top: 0;
    flex-direction: column;
    margin-bottom: 28px;
  }

  @media screen and (max-width: 1024px) {
    .dashboard {
      flex-direction: column-reverse;
    }

    .charts {
      overflow-x: auto;
    }
  }
</style>
