<script>
import axios from 'axios';
import { debuglog } from 'util';
import Employees from './Employees.vue';

export default {
  components: {
    Employees,
  },
  props: ['dateInput'],
  data() {
    return {
      date: '',
      tokenStr: '',
      menu: false,
      headers: [
        { text: 'Job', align: 'left', sortable: false, value: 'job' },
        { text: 'Monday', sortable: false, value: 'monday' },
        { text: 'Tuesday', sortable: false, value: 'tuesday' },
        { text: 'Wednesday', sortable: false, value: 'wednesday' },
        { text: 'Thursday', sortable: false, value: 'thursday' },
        { text: 'Friday', sortable: false, value: 'friday' },
        { text: 'Saturday', sortable: false, value: 'sunday' },
        { text: 'Sunday', sortable: false, value: 'sunday' },
      ],
      hours: [],
      Id: '',
      jobs: [],
      job: '',
			show: false,
			tableRef: undefined
    };
  },

  computed: {
    
    total () {
      var total = 0;
      for(var i = 0; i < this.hours.Hours.length;i = i + 1) {
        total = total + parseFloat(this.hours.Hours[i].monday.Hours) + parseFloat(this.hours.Hours[i].tuesday.Hours) + parseFloat(this.hours.Hours[i].wednesday.Hours) + parseFloat(this.hours.Hours[i].thursday.Hours) + parseFloat(this.hours.Hours[i].friday.Hours) + parseFloat(this.hours.Hours[i].saturday.Hours) + parseFloat(this.hours.Hours[i].sunday.Hours)
      }

      return total
    },
    dayTotal() {
      var total = {
        Monday: 0.0,
        Tuesday: 0.0,
        Wednesday:0.0,
        Thursday:0.0,
        Friday:0.0,
        Saturday:0.0,
        Sunday:0.0,
      };

      for(var i = 0; i < this.hours.Hours.length;i = i + 1) {
        total.Monday += parseFloat(this.hours.Hours[i].monday.Hours)
		console.log(total.Monday)
        total.Tuesday += parseFloat(this.hours.Hours[i].tuesday.Hours)
        total.Wednesday += parseFloat(this.hours.Hours[i].wednesday.Hours)
        total.Thursday += parseFloat(this.hours.Hours[i].thursday.Hours)
        total.Friday += parseFloat(this.hours.Hours[i].friday.Hours)
        total.Saturday += parseFloat(this.hours.Hours[i].saturday.Hours)
        total.Sunday += parseFloat(this.hours.Hours[i].sunday.Hours)
      }
	  console.log(total)
      return total;
    }

  },

  methods: {
    updateDate(date, Id) {
      console.log(date);
      this.date = date;
      this.Id = Id;
      this.importData();
    },
    save(date) {
      this.$refs.menu.save(date);
      this.importData();
    },
    importData() {
      const path = 'http://13.55.48.180:5000/hours/get';
      const payload = {
        Date: this.date,
        ID: this.Id,
        filters: this.job
      };
      axios.post(path, payload)
        .then((res) => {
          this.hours = res.data;
        })
        .catch((error) => {
    		axios.defaults.headers.common.Authorization = `Bearer ${localStorage.getItem('token')}`;
    		this.Id = localStorage.getItem('Id')
			axios.post(path, payload)
        	.then((res) => {
          		this.hours = res.data;
        	})
		});
    },
    exportData() {
      const path = 'http://13.55.48.180:5000/hours/add';
      const date_send = {
        Date: this.date,
      };
      console.log(this.date);
      const payload = {
        timeData: this.hours,
        date: this.date,
        ID: this.Id,
      };
      axios.post(path, payload)
        .then(() => {
          this.importData();
          this.$store.dispatch('getAllJobs')
        })
        .catch((error) => {
          // eslint-disable-next-line
          console.log(error);
          this.importData();
        });
    },
    getJobs () {
      const path = 'http://13.55.48.180:5000/job/combo'
      const payload = {
        ID: this.Id
      }
      axios.post(path,payload)
        .then((res)=>{
          this.jobs = res.data
        })
    }
  },
  mounted() {
    this.date = `${(new Date()).getFullYear()}-${(new Date()).getMonth() + 1}-${(new Date()).getDate()}`;
    axios.defaults.headers.common.Authorization = `Bearer ${localStorage.getItem('token')}`;
    this.Id = localStorage.getItem('Id');
    this.importData();
		this.getJobs();
		// document.getElementsByClassName('v-table__overflow')[0].addEventListener('click', () => {
		// 	console.log('asdf');
		// });

