<template>
  <div>
    <v-container align="right">
      <v-layout justify-center>
        <v-flex xs1 sm10 class="pa-1">
          <table class="table table-hover">
            <thead>
              <tr>
                <th>Name</th>
                <th>Date</th>
                <th>Amount</th>
                <th
                  scope="col"
                  width="50px" />
              </tr>
            </thead>
            <tbody>
              <tr v-for="(invoice, index) in invoices" :key="index">
                <td>
                  <v-text-field v-model="invoice.Name" label="Name" color="blue" />
                </td>
                <td>
                  <!-- <v-text-field v-model="invoice.Date"  v-mask='mask' label="Date(YYY-MM-DD)" color="blue" /> -->
                  <v-menu
                    ref="menu"
                    v-model="menu[invoices.indexOf(invoice)]"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="invoice.Date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on }">
                      <v-text-field
                        v-model="invoice.Date"
                        label="Date (YYYY-MM-DD)"
                        readonly
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker v-model="invoice.Date" no-title scrollable>
                      <v-spacer></v-spacer>
                      <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                      <v-btn flat color="primary" @click="$refs.menu[index].save(invoice.Date)">OK</v-btn>
                    </v-date-picker>
                  </v-menu>
                </td>
                <td>
                  <v-text-field prefix='$' type='number' v-model="invoice.Amount" label="Amount" color="blue" />
                </td>
                <td class="box">
                  <v-checkbox v-model="invoice.select" color="blue" hide-details />
                </td>
              </tr>
            </tbody>
          </table>
          <v-btn @click="add">Add</v-btn>
          <v-btn outline color="red" @click="remove">Remove</v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import { constants } from "crypto";
import {mask} from 'vue-the-mask'
export default {
 directives: {
  mask
 }, 
 components: {},
  props: {},
  data() {
    return {
      mask: "####-##-##",
      menu: []
    };
  },

  computed: {
    invoices() {
      return this.$store.getters.getJobInvoices;
    }
  },
  watch: {
    invoices: {
      deep: true,
      handler() {
        this.$store.commit("editInvoices", this.invoices);
      }
    }
  },

  methods: {
	add() {
      var invoice = {
        Name: "",
        Date: "",
        Amount: ""
      };
      this.invoices.push(invoice);
    },
    remove() {
      let i;
      const length = parseInt(this.invoices.length);
      for (i = length - 1; i >= 0; i--) {
        if (this.invoices[i].select == true) {
          this.invoices.splice(i, 1);
        }
      }
    }
  }
};
</script>

<style>
table {
  width: calc(100% + 30px);
  table-layout: fixed;
  border-spacing: 15px 0px;
  margin: 0px -15px;
}

.box {
  width: 30px;
}

span.b {
  width: 400px;
  display: inline-block;
  padding: 5px;
}
</style>
