  position: -webkit-sticky;
  position: sticky;
  top: 100px;
  padding: 5px;
  justify-content: center;
  background-color: rgb(250, 250, 250);
  display: flex;
}

.col {
  width: 400;
  margin: 1px;
}
.colB {
  width: 50px;
  margin: 1px;
}
.pointer {
  cursor: pointer;
}
</style>
